::-webkit-scrollbar {
    width: 0;
}

div.vkuiPanelHeader__right.PanelHeader__right > button {
    margin-right: 8px;
}

div.vkuiPanelHeader__left.PanelHeader__left > div > button {
    margin-left: 8px;
}

#shadow {
    display: none;
    background: black;
    width: 10000px;
    height: 10000px;
    position: absolute;
    z-index: 100;
    opacity: 0;
    pointer-events: none;
}

.shadow {
    animation-name: shadow;
    animation-duration: 2s;
}

@keyframes shadow {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    75% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }

}